import React from "react";
import logo from "../images/logo.png"
import profile from "../images/profile-1.jpg" 
import "../CSSs/navcss.css"

function Nav(props) {
    return (
         <nav>
             <div className="container">
             <img src={logo} className="logo"></img>
             <div className="search-bar">
             <span class="material-symbols-sharp">
             search
             </span>
             <input type="search" placeholder="Search"></input>
             </div>
             <div className="profile-area">
             <div className="theme-btn" onClick={props.theme} >
             <span className={props.THEME?"material-symbols-sharp":"material-symbols-sharp active"}>
             light_mode
             </span>
             <span class={props.THEME?"material-symbols-sharp active":"material-symbols-sharp"}>
             dark_mode
             </span>
             </div>
             <div className="profile">
                   <div className="profile-photo">
                       <img src={profile}></img>
                   </div>
                   <h5>Chu Hua</h5>
                   <span class="material-symbols-sharp">
                    expand_more
                   </span>
             </div>
             <button id="menu-btn" onClick={props.function}>
             <span class="material-symbols-sharp">
               menu
             </span>
             </button>
             </div>
             </div>
         </nav>
    );
  }
  
  export default Nav;

