 import React from "react";
 import Aside from "./aside";
 import Middle from "./middle";
 import Right from "./right";
 import "../CSSs/main.css"

export default function Main(props){
    return <main>
       <Aside function={props.function} isOpen={props.isOpen} />
       <Middle />
       <Right />
    </main>
   
    
}